@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

 
.card {
  background: #fff;
  border-radius: 5px;
  height: 175px;
  margin: 1rem;
  position: relative;
  width: 175px;
  box-shadow: 0 3px 6px rgba(0,0,0,.16),0 3px 6px rgba(0,0,0,.23);
}

.card:hover{
  transform: scale(1.05);
  box-shadow: 0 14px 28px rgba(164,48,41,.25),0 10px 10px rgba(164,48,41,.22);
  transition: .1s
}

.card > .img-container {
  border-radius: 10px;
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: linear-gradient(#151515, #282727);
  border: #a43029 1.5px solid;
}

.card > .img-container > img {
  margin-top: 5%;
  width: 83%
}

.shake {
  -webkit-animation: shake .5s .0125s;
  animation: shake .5s .0125s
}

@-webkit-keyframes shake {
  0%,to {
      transform: translateZ(0)
  }

  12.5%,37.5%,62.5%,87.5% {
      transform: translate(-10px)
  }

  25%,50%,75% {
      transform: translate(10px)
  }
}

@keyframes shake {
  0%,to {
      transform: translateZ(0)
  }

  12.5%,37.5%,62.5%,87.5% {
      transform: translate(-10px)
  }

  25%,50%,75% {
      transform: translate(10px)
  }
}
.navbar {
    background: #414040;
    border-bottom: 1px solid #a43029;
    font-family: 'Audiowide';
    font-size: medium;
    min-height: 70px;
    position: fixed;
    width: 100%;
    color: #fff;
    font-size: calc(10px + 14 * (100vw - 320px)/800);
    z-index: 99;
    white-space: nowrap;
}

.navbar ul {
    margin: 0;
    padding: 0
  }
  
  .navbar li {
    display: inline-block;
    padding: 26px 0;
    width: 33.3%;
    text-align: center
  }
  
  .navbar a {
    text-decoration: none;
    color: #fff;
    font-weight: 900
  }
  
  .score {
    margin: 0 2.5%;
  }
  
  .splitScore {
    color: #a43029;
  }
  
  .navbar .correct {
      color: #2e7d32;
    -webkit-animation: 1s correct;
    animation: 1s correct
  }
  
  .navbar .incorrect {
      color: #c62828;
    -webkit-animation: 1s incorrect;
    animation: 1s incorrect
  }
  
  @-webkit-keyframes correct {
    0% {
        color: #fff
    }
  
    20% {
        color: #2e7d32
    }
  
    40% {
        color: #fff
    }
  
    60% {
        font-weight: #2e7d32
    }
  
    80% {
        font-weight: #fff
    }
  
    to {
        font-weight: #2e7d32
    }
  }
  
  @keyframes correct {
    0% {
        color: #fff
    }
  
    20% {
        color: #2e7d32
    }
  
    40% {
        color: #fff
    }
  
    60% {
        font-weight: #2e7d32
    }
  
    80% {
        font-weight: #fff
    }
  
    to {
        font-weight: #2e7d32
    }
  }
  
  @-webkit-keyframes incorrect {
    0% {
        color: #fff
    }
  
    20% {
        color: #c62828
    }
  
    40% {
        color: #fff
    }
  
    60% {
        font-weight: #c62828
    }
  
    80% {
        font-weight: #fff
    }
  
    to {
        font-weight: #c62828
    }
  }
  
  @keyframes incorrect {
    0% {
        color: #fff
    }
  
    20% {
        color: #c62828
    }
  
    40% {
        color: #fff
    }
  
    60% {
        font-weight: #c62828
    }
  
    80% {
        font-weight: #fff
    }
  
    to {
        font-weight: #c62828
    }
  }
